<template>
    <div :class="prefixCls">
        <at-ta
            :members="paramPersonList"
            name-key="label"
            @input="onchangeValue"
        >
            <!-- custom: with avatars -->
            <template v-slot:item="s">
                <span v-text="s.item.label"></span>
            </template>

            <textarea
                :style="{height: height, minHeight: minHeight, width: width}"
                v-model="showText"
                :id="editorId"
                :placeholder="placeholder"
                :maxlength="maxlength"
                class="element-editor"
            ></textarea>
            <!-- <el-input
                type="textarea"
                v-model="showText"
                class="element-editor"
                :id="editorId"
                :placeholder="placeholder"
                :style="{height: height, minHeight: minHeight, width: width}"
                :maxlength="maxlength"
            >
            </el-input>-->
        </at-ta>
    </div>
</template>

<script>
    import AtTa from './at-textarea.vue'

    export default {
        name: "vue-at-person-editor",

        components: {
            AtTa
        },

        props: {
            //
            placeholder:{
                type:String
            },

            //
            width:{
                type:String,
                default:'300px'
            },

            //
            height:{
                type:String,
                default:''
            },

            //
            minHeight:{
                type:String,
                default:'50px'
            },

            // top 
            placement:{
                type:String,
                default:'top'
            },

            //
            paramPersonList:{
                type:Array,
                default:() => []
            },

            //
            maxlength:{
                type:Number
            }
        },

        data() {
            return {
                prefixCls: 'components-at-person-editor-index',
                editorId:'components-at-person-editor',
                showText:'',
            }
        },

        computed: {},

        watch: {},

        created() {
            this.editorId = this.editorId + (new Date()).getTime(); //id
        },

        mounted() {
            document.querySelector(`#${this.editorId}`).style.height = this.height;
            document.querySelector(`#${this.editorId}`).style.minHeight = this.minHeight;
        },

        beforeDestroy() {
        },

        methods: {
            onchangeValue(val){
                let str = val;
                let data = [];
                str = str.replace(/(@[^@\s]+\s)/g, ($1) => {
                    const label = $1.substr(1, $1.length - 2); //@"@ "
                    const value = (this.paramPersonList.find(item => item.label === label) || {}).value || '';
                    //
                    if(value){
                        data.push({
                            value: value,
                            label: label
                        });
                        return `<span class="person-at-label" data-label="${label}" data-value="${value}">${$1}</span>`;
                    }
                    return `${$1}`;
                })
                this.$emit('on-change', str);
                this.$emit('on-change-person', data);
            },

            //
            setValue(val){
                if(!val) {
                  return false;
                }
                const str = val.replace(/<[^>]+>/g, ''); //html
                this.showText = str;
            },

            //
            clearEditorContent(){
                this.showText = '';
            },

        }
    }
</script>

<style lang="scss">
$prefixCls:components-at-person-editor-index;
.#{$prefixCls}{
  .el-textarea{
    textarea{
      width: 100%;
      height: 100%;
    }
  }
}
</style>
